import React, { useRef, useState } from "react"
import Recaptcha from "react-recaptcha"
import { useForm } from "react-hook-form"
import axios from "axios"

import { Button, StyledForm, FormField } from "../style"
import { Layout } from "../components/Layout"
import { Page } from "../components/Page"
import { Title } from "../components/Title"
import { Quote } from "../components/Quote"

export default function ContactPage() {
  const { register, handleSubmit } = useForm()
  const recaptchaRef = useRef(null)
  const [status, setStatus] = useState("INIT") // [INIT, SUBMITTING, DONE, ERROR]

  const onSubmit = async (formData, recaptchaToken) => {
    setStatus("SUBMITTING")
    axios
      .post(process.env.CONTACT_FORM_URL, {
        formData,
        recaptcha: recaptchaToken,
      })
      .then(() => {
        setStatus("DONE")
      })
      .catch(() => {
        setStatus("ERROR")
        recaptchaRef.current.reset()
      })
  }

  const executeCaptcha = e => {
    e.preventDefault()
    recaptchaRef.current.execute()
  }

  return (
    <Layout>
      <Page metaTitle="Contact">
        <Title>Contact Us</Title>
        <p>
          Whether you have a question about Takiwā Data Analytics Platform,
          pricing, need a demo, or anything else, we are ready to answer all
          your questions.
        </p>
        {status === "DONE" ? (
          <Quote>
            Thank you for your message! <br />
            <br />
            Someone from the Takiwā Team will be in touch with you shortly.
          </Quote>
        ) : (
          <StyledForm name="contact" onSubmit={executeCaptcha}>
            <FormField>
              <label htmlFor="firstName">First Name</label>
              <input
                ref={register}
                id="firstName"
                name="firstName"
                type="text"
                required
              />
            </FormField>
            <FormField>
              <label htmlFor="lastName">Last Name</label>
              <input ref={register} id="lastName" name="lastName" type="text" />
            </FormField>
            <FormField>
              <label htmlFor="fromEmail">Email</label>
              <input
                ref={register}
                id="fromEmail"
                name="fromEmail"
                type="text"
                required
              />
            </FormField>
            <FormField>
              <label htmlFor="phone">Phone</label>
              <input ref={register} id="phone" name="phone" type="text" />
            </FormField>

            <FormField wide>
              <label htmlFor="message">Message</label>
              <textarea
                ref={register}
                cols="40"
                rows="5"
                id="message"
                name="message"
                required
              />
            </FormField>

            <Recaptcha
              ref={recaptchaRef}
              sitekey={process.env.RECAPTCHA_KEY}
              size="invisible"
              verifyCallback={handleSubmit(onSubmit)}
              theme="dark"
            />

            {status === "ERROR" && (
              <FormField wide>
                <Quote>Something went wrong. Please try again.</Quote>
              </FormField>
            )}

            <FormField wide>
              <Button
                primary
                type="submit"
                value="Submit"
                disabled={status === "SUBMITTING"}
              >
                Submit
              </Button>
            </FormField>
          </StyledForm>
        )}
      </Page>
    </Layout>
  )
}
